// Assets
import right from '../../../assets/icons/arrow-right.svg'
import left from '../../../assets/icons/arrow-left.svg'

export default class extends React.Component {
    constructor(props) {
	    super(props)
	}
    render() {
    	const {event, currentpagenumber} = this.props
    	let nextPage = currentpagenumber + 1
    	let prevPage = currentpagenumber - 1

	    return (
	        <div> 
	        	<img src={right} alt="Página siguiente" onClick={e => event({nextPage})} /> 	
	        	<img src={left} alt="Página anterior" onClick={e => event({prevPage})} />	        	   
	        	<style jsx>{`
	        		img{
	        			float:right;
	        			padding-left:12px;
	        			cursor:pointer;
	        		}
	        	`}</style>      
	        </div>
	    );
	}
}


